    opacity: 0.95;
  }
  .LightMainContent .NavBar:hover {
    background-color: beige;
    /* box-shadow: 0px 5px 30px 10px rgb(100,100,100); */
    box-shadow: 0px 5px 10px 10px rgb(200, 200, 200);
    opacity: 0.95;
  }

  .NavBar img {
    height: 50px;
    width: 50px;
    object-fit: cover;
    border-radius: 25px;
    margin-right: 15px;
  }

  .NavLeft {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .NavLeft * {
    display: inline-block;
  }

  .NavBar h3 {
    margin-right: 0px;
